<template>
	<transition name="toast-fade">
		<div class="toast" :class="[toastCls,positionCls]" :style="{'padding':iconCls != '' ? '0.3rem':'.2rem'}" v-show="visible && toastTxt">
			<i class="toast-icon" :class="'toast-icon-'+iconCls" v-if="iconCls != ''"></i>
			<span class="toast-txt" :style="{'padding-top':iconCls != '' ? '10px':'0'}">{{ toastTxt }}</span>
		</div>
	</transition>
</template>

<script>
	export default {
		data(){
			return {
				visible:!1
			}
		},
		props:{
			toastTxt:{
				type:String
			},
			toastCls:{
				type:String,
				default:''
			},
			iconCls:{
				type:String,
				default:''
			},
			position:{
				type:String,
				default:'center'
			},
			duration:{
				type:Number,
				default:1500
			}
		},
		computed:{
			positionCls(){
				const positionClasses = {
					center:'pos-center',
					left:'pos-left',
					right:'pos-right',
					top:'pos-top',
					bottom:'pos-bottom'
				};
				return positionClasses[this.position];
			}
		}
	}
</script>

<style>
	@font-face{
		font-family:  "iconfont";
		src:  url('data:application/x-font-woff;charset=utf-8;base64,') format('woff'),
		url('../../../assets/font/iconfont.ttf?t=1510207276915') format('truetype'), /* chrome, firefox, opera, Safari, Android, iOS 4.2+*/
		url('../../../assets/font/iconfont.svg?t=1510207276915#iconfont') format('svg'); /* iOS 4.1- */
	}
	.toast-icon {
		font-family:"iconfont" !important;
		font-size:30px;
		font-style:normal;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
	}

	.toast-icon-info:before { content: "\e6e5"; }
	.toast-icon-warning:before { content: "\e659"; }
	.toast-icon-success:before { content: "\e657"; }

	.toast{
		position:fixed;
		
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;

		max-width: 75%;
		border-radius: 0.06rem;
		
		color:#fff;
		font-size:14px;
		line-height: 1.5;
		background-color: rgba(0,0,0,.75);
		
		
		transition: opacity .3s linear;

		z-index: 99998;
	}
	
	.toast.pos-center{
		left:50%;
		top:50%;
		transform: translate3d(-50%,-50%,0);
	}

	.toast.pos-top{
		left:50%;
		top:.5rem;
		transform: translate3d(-50%,0,0);
	}

	.toast.pos-right{
		right:.5rem;
		top:50%;
		transform: translate3d(0,-50%,0);
	}

	.toast.pos-bottom{
		left:50%;
		bottom:.5rem;
		transform: translate3d(-50%,0,0);
	}

	.toast.pos-left{
		left:.5rem;
		top:50%;
		transform: translate3d(0,-50%,0);
	}
	
	.toast-txt{
		text-align: center;
	}

	.toast-fade-enter,.toast-fade-leave-to{
		opacity: 0;
	}
</style>


